import React, { useState } from "react";
import {
  Box,
  Container,
  Grid,
  Typography,
  useMediaQuery,
  useTheme,
  Card,
} from "@mui/material";
import Balance from "../../../components/Balance";
import { makeStyles } from "@mui/styles";
import CabinetTop from "../../../components/cabinetTop";
import { useDispatch, useSelector } from "react-redux";
import Diagram from "../../../components/chart";
import { getСurrenciesListFiat } from "../../../redux/reducers/Сurrencies";

const AllCourses = ({ t }) => {
  const useStyles = makeStyles((theme) => ({
    root: {},
    courseTableHead: {
      borderTop: "2px solid #E7EAEE",
      padding: "24px 0 24px",
      marginTop: 34,
      background: "transparent",
      [theme.breakpoints.down(992)]: {
        padding: "20px 40px 24px",
      },
      [theme.breakpoints.down(576)]: {
        padding: "20px 14px 24px",
      },
    },
    courseTableHeadCell: {
      fontSize: "20px",
      lineHeight: "24px",
      color: "#64748B",
      border: "none",
      textAlign: "center",
    },

    courseTableRow: {
      boxShadow: "0px 1px 0px #E2E8F0",
      borderRadius: "16px",
      padding: "16px 0 16px 0",
      marginBottom: "8px",
      [theme.breakpoints.down(992)]: {
        padding: "16px 40px 16px 40px",
      },
      [theme.breakpoints.down(576)]: {
        padding: "16px 15px 16px 15px",
      },
    },

    courseTableBoldText: {
      color: "inherit",
      fontWeight: 600,
      fontSize: "19px",
      lineHeight: "23px",
      textAlign: "center",
      [theme.breakpoints.down(992)]: {
        textAlign: "left",
        fontSize: "28px",
      },
      [theme.breakpoints.down(576)]: {
        fontSize: "23px",
      },
      [theme.breakpoints.down(320)]: {
        fontSize: "20px",
      },
    },

    courseCoinDynamic: {
      fontWeight: 600,
      fontSize: "19px",
      lineHeight: "23px",
      textAlign: "center",
      background: "#ECFDF5",
      padding: "8px 12px",
      borderRadius: "8px",
      color: "#10B981",
    },
    courseCoinImgWrapper: {
      marginRight: "16px",
      [theme.breakpoints.down(992)]: {
        marginRight: "20px",
      },
      [theme.breakpoints.down(576)]: {
        marginRight: "8px",
      },
    },
    courseTableBodyRow: {
      background: "#fff",
      padding: "16px 32px",
      boxShadow: "0px 1px 0px #E2E8F0",
      borderRadius: "16px",
      marginBottom: "16px",
    },
    courseTableCoinCell: {
      justifyContent: "flex-start",
      paddingLeft: "60px",
      [theme.breakpoints.down(992)]: {
        paddingLeft: "0",
      },
    },
    courseButton: {
      display: "block",
      margin: "24px auto 0 auto",
    },
    courseTableCoinNames: {
      display: "flex",
      [theme.breakpoints.down(992)]: {
        flexDirection: "column",
      },
    },
    courseTableRowContanier: {
      [theme.breakpoints.down(992)]: {
        flexWrap: "nowrap",
      },
    },
    courseTableCoinPrice: {
      [theme.breakpoints.down(992)]: {
        justifyContent: "flex-end",
      },
    },
    courseCoinFullName: {
      color: "#64748B",
      fontWeight: "400",
      [theme.breakpoints.down(992)]: {
        marginTop: "4px",
      },
    },
  }));
  const classes = useStyles();
  const dispatch = useDispatch();
  const theme = useTheme();
  const tablet = useMediaQuery(theme.breakpoints.down(992));

  const { СurrenciesList, СurrenciesListFiat } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );
  React.useEffect(() => {
    dispatch(getСurrenciesListFiat());
    // eslint-disable-next-line
  }, []);
  const [filter, setFilter] = useState("");
  const fiat = Object.values(
    typeof СurrenciesListFiat?.currencies === "object" &&
      СurrenciesListFiat?.currencies
  );

  const changedFiat = fiat?.map((data) => ({
    ...data,
    type: "fiat",
  }));

  const allCurrency = changedFiat
    .concat(СurrenciesList)
    .filter((item) => (item.currency !== "USD") & (item.currency !== "USDT"));

  const filteredCoin = React.useMemo(
    () =>
      allCurrency.filter((item) =>
        item.currency
          .toLocaleLowerCase()
          .startsWith(filter.toLocaleLowerCase().trim())
      ),
    // eslint-disable-next-line
    [filter]
  );
  const Сurrencies = filter ? filteredCoin : allCurrency;

  return (
    <section>
      <Container maxWidth="lg">
        <Balance />
        <CabinetTop
          title={t("exchangeRates")}
          input
          courses
          last_update={СurrenciesList?.last_update}
          setFilter={setFilter}
          t={t}
        />
        <Container>
          <Grid
            className={classes.courseTableHead}
            container
            direction="row"
            justifyContent="space-between"
            alignItems="center"
          >
            <Grid item md={3}>
              <Typography className={classes.courseTableHeadCell}>
                {t("coinTitle")}
              </Typography>
            </Grid>
            <Grid item md={3}>
              <Typography className={classes.courseTableHeadCell}>
                {t("price")}
              </Typography>
            </Grid>
            {!tablet && (
              <>
                <Grid item md={3}>
                  <Typography className={classes.courseTableHeadCell}>
                    {t("market")}
                  </Typography>
                </Grid>
                <Grid item md={3}>
                  <Typography className={classes.courseTableHeadCell}>
                    {t("dynamic")}
                  </Typography>
                </Grid>
              </>
            )}
          </Grid>
          <Box>
            {Array.isArray(Сurrencies) &&
              Сurrencies
                .filter((item) => item.rate)
                .map((coin, index) => {
                  return (
                    <Card className={classes.courseTableRow} key={index}>
                      <Grid
                        className={classes.courseTableRowContanier}
                        container
                        direction="row"
                        justifyContent="space-between"
                        alignItems="center"
                      >
                        <Grid
                          className={classes.courseTableCoinCell}
                          item
                          md={3}
                          container
                          direction="row"
                          justifyContent="center"
                          alignItems="center"
                        >
                          <Box sx={{ display: "flex", alignItems: "center" }}>
                            <Box className={classes.courseCoinImgWrapper}>
                              <img
                                style={{ objectFit: "contain" }}
                                width="40"
                                height="40"
                                src={coin.logo}
                                alt="Coin icon"
                              />
                            </Box>
                            <Box className={classes.courseTableCoinNames}>
                              <Box mr={1}>
                                <Typography
                                  className={classes.courseTableBoldText}
                                >
                                  {coin.type === "fiat" && "USDT/"}
                                  {coin?.currency}
                                </Typography>
                              </Box>
                              <Typography
                                className={classes.courseCoinFullName}
                              >
                                {coin.type !== "fiat" && coin?.name}
                              </Typography>
                            </Box>
                          </Box>
                        </Grid>

                        <Grid
                          className={classes.courseTableCoinPrice}
                          item
                          md={3}
                          container
                          direction="row"
                          justifyContent="center"
                          alignItems="center"
                        >
                          <Typography className={classes.courseTableBoldText}>
                            {coin.type !== "fiat" && "$"}
                            {coin?.rate} {coin.type === "fiat" && coin?.name}
                          </Typography>
                        </Grid>

                        {!tablet && (
                          <>
                            <Grid
                              item
                              md={3}
                              container
                              direction="row"
                              justifyContent="center"
                              alignItems="center"
                            >
                              <Box
                                className={classes.courseCoinDynamic}
                                style={{
                                  background:
                                    coin?.difference &&
                                    coin.difference[0] === "+"
                                      ? "#ECFDF5"
                                      : coin?.difference &&
                                        coin.difference[0] === "-"
                                      ? "#FEF2F2"
                                      : "#F7F8F9",
                                  color:
                                    coin?.difference &&
                                    coin.difference[0] === "+"
                                      ? "#10B981"
                                      : coin?.difference &&
                                        coin.difference[0] === "-"
                                      ? "#EF4444"
                                      : "#A0ABBB",
                                }}
                              >
                                <Typography
                                  color="inherit"
                                  className={classes.courseTableBoldText}
                                >
                                  {coin?.difference === undefined
                                    ? "0.00"
                                    : coin?.difference}
                                </Typography>
                              </Box>
                            </Grid>
                            <Grid
                              item
                              md={3}
                              container
                              direction="row"
                              justifyContent="center"
                              alignItems="center"
                            >
                              <Box style={{ width: 100 }}>
                                <Diagram
                                  height={37}
                                  color={
                                    coin?.difference &&
                                    coin.difference[0] === "+"
                                      ? "#10B981"
                                      : coin?.difference &&
                                        coin.difference[0] === "-"
                                      ? "#F15A5A"
                                      : "#C1C7D2"
                                  }
                                  rates={coin.rates}
                                />
                              </Box>
                            </Grid>
                          </>
                        )}
                      </Grid>
                    </Card>
                  );
                })}
          </Box>
        </Container>
      </Container>
    </section>
  );
};
export default AllCourses;
